<template>
  <div class="p-4 rounded flex justify-between" :class="colours">
    <div><span class="font-bold">{{ modeName }}</span><span v-if="started"> - {{ timeLeft }}</span></div>
    <span>Session {{session}}/{{totalSessions}}</span>
  </div>
</template>

<script>
import modes from "../services/modes";

export default {
  name: "Mode",
  props: {
    mode: {
      type: Number
    },
    secondsLeft: {
      type: Number
    },
    session: {
      type: Number, // TODO: could show n' little icons..unicdoe guy running maybe?
    },
    totalSessions: {
      type: Number,
    },
  },

  computed: {
    modeName() {
      const names = {
        [modes.STOPPED]: "Get ready...",
        [modes.WRITING]: "SPRINT!",
        [modes.EDITING]: "Pause, edit, relax",
        [modes.DIED]: "What happened? Wh-where is my chapter??",
      };
      return names[this.mode];
    },
    colours() {
      const colours = {
        [modes.STOPPED]: "bg-blue-700 text-blue-100",
        [modes.WRITING]: "text-red-100 bg-red-700",
        [modes.EDITING]: "bg-green-700 text-green-100",
        [modes.DIED]: "text-gray-50 bg-black"
      };
      return colours[this.mode];
    },
    timeLeft() {
      return new Date(this.secondsLeft * 1000).toISOString().substr(14, 5);
    },
    started() {
      return this.mode !== modes.STOPPED && this.mode !== modes.DIED;
    }
  }
};
</script>