<template>
  <div id="app" :class="dark ? 'dark' : ''">
    <div
      class="
        h-screen
        bg-white
        dark:bg-gray-800
        text-gray-800
        dark:text-gray-100
        p-4
        md:p-10
        flex flex-col
      "
    >
      <div class="flex justify-between gap-4 w-full">
        <p class="p-2 w-1/6 md:block hidden"></p>
        <Mode
          class="w-full md:max-w-screen-md"
          :mode="currentMode"
          :secondsLeft="secondsLeft"
          :session="currentSession"
          :totalSessions="totalSessions"
        />
        <div class="w-1/6 py-4 content-end">
          <button class="pr-4" @click="toggleDarkMode">
            <icon :icon="dark ? 'sun' : 'moon'" />
          </button>
          <a href=""><icon icon="question-circle" /></a>
        </div>
      </div>
      <!-- TODO: flex to make editor fill space, then bottom bar, and use overflow-y to scroll editor -->
      <Editor
        class="filter flex-grow overflow-auto"
        :style="editorStyle"
        v-model="content"
        @wc="updateWordCount"
      />
      <div class="mx-auto w-full md:max-w-screen-md flex items-end justify-between p-4">
        <div class="text-blue-300 dark:text-gray-700 text-sm">v0.1 Beta</div>
        <div class="font-bold">{{ wc }} words</div>
        <div class="text-blue-300 dark:text-gray-700 text-sm">
          <a href="https://twitter.com/egocv">contact @egocv</a>
        </div>
        <!-- TODO: on session end, pop up "Wrote x words in session"  this is v2!!!! releaaaase -->
      </div>
    </div>
  </div>
</template>

<script>
import Editor from "./components/Editor.vue";
import Mode from "./components/Mode.vue";
import modes from "./services/modes";

export default {
  name: "App",
  components: {
    Editor,
    Mode
  },

  // TODO: check phone, less h-padding when on small screen?
  // TODO: settings button next to dark mode button (or put that in the settings?)

  // TODO: definitely should not be able to undo after dying, because that just brings back your text!
  // TODO: disable undo during writing!!

  // TODO: when the session is complete, need to stop fully until a button is clicked, so it doesn't just relaunch
  // TODO: the next session right away!

  // TODO: would be good if we stopped you just typing 'a...a....a' to keep it from deleting. Perhaps you need to type
  // actual words to refill your bar, as it were. Think about this.

  data() {
    return {
      content: "<p></p>",
      wc: 0,
      dark: false, // TODO: localstorage remember setting + respect OS setting https://tailwindcss.com/docs/dark-mode
      sessionStart: 0,
      died: false,
      timePassed: null, // manually updated
      sprintLength: 60 * 5, // TODO: we could do a nice little progress bar thingy with red/blue blocks for payuse/write
      pauseLength: 60 * 2,
      totalSessions: 3,
      blurAfter: 4, // blur 5 seconds before deletion
      deleteAfter: 8,
      lastEdit: 0
    };
  },

  created() {
    setInterval(() => {
      this.setTimePassed();
    }, 100);
  },

  computed: {
    secondsPassed() {
      return Math.floor(this.timePassed / 1000);
    },
    // TODO: unit tests for this sort of thing, some of these seem to be off by 1s, make some tests
    sessionLength() {
      return this.sprintLength + this.pauseLength;
    },
    currentSession() {
      return Math.ceil(this.secondsPassed / this.sessionLength) || 1;
    },
    currentMode() {
      if (this.died) {
        return modes.DIED;
      }
      if (!this.sessionStart) {
        return modes.STOPPED;
      }
      return this.secondsPassed % this.sessionLength <= this.sprintLength
        ? modes.WRITING
        : modes.EDITING;
    },
    started() {
      return (
        this.currentMode !== modes.STOPPED && this.currentMode !== modes.DIED
      );
    },
    secondsLeft() {
      const secondsPassed = this.secondsPassed % this.sessionLength;
      if (this.currentMode === modes.WRITING) {
        return this.sprintLength - secondsPassed;
      }
      return this.sessionLength - secondsPassed;
    },
    editorStyle() {
      if (this.currentMode === modes.WRITING) {
        const secondsSinceLastEdit = (Date.now() - this.lastEdit) / 1000;
        const maxBlur = 7;
        if (secondsSinceLastEdit > this.blurAfter) {
          const blurPct =
            (secondsSinceLastEdit - this.blurAfter) /
            (this.deleteAfter - this.blurAfter); // % of total blur
          return `--tw-blur: blur(${maxBlur * blurPct}px)`;
        }
      }
      return "";
    }
  },

  // TODO: linting, always commas etc.

  methods: {
    toggleDarkMode() {
      this.dark = !this.dark;
    },

    startSession() {
      this.sessionStart = Date.now();
      this.died = false;
    },

    setTimePassed() {
      if (this.started) {
        this.timePassed = Date.now() - this.sessionStart;
      }

      if (this.timePassed / 1000 > this.totalSessions * this.sessionLength) {
        this.finish();
      }

      const secondsSinceLastEdit = this.getSecondsSinceLastEdit();
      if (secondsSinceLastEdit > this.deleteAfter) {
        this.delete();
      }
    },

    getSecondsSinceLastEdit() {
      return this.currentMode === modes.WRITING
        ? (Date.now() - this.lastEdit) / 1000
        : 0;
    },

    finish() {
      this.sessionStart = 0;
    },

    delete() {
      this.content = "<p></p>";
      this.died = true;
      this.$nextTick(this.finish);
    },

    modeChanged() {
      console.log("mode changed");
      this.lastEdit = Date.now();
    },

    updateWordCount(wc) {
      this.wc = wc;
    }
  },

  watch: {
    currentMode(to, from) {
      this.modeChanged(to, from);
    },
    content() {
      // start session when content changes
      if (this.sessionStart === 0) {
        this.startSession();
      }
      this.lastEdit = Date.now();
    }
  }
};
</script>

