import { Extension } from '@tiptap/core';

const WordCount = Extension.create({
    name: 'wordCount',
    addCommands() {
        return {
            getWordCount: () => function ({ state }) {
                // https://prosemirror.net/docs/ref/#model.Node.descendants
                let wordCount = 0;
                state.doc.descendants((node) => {
                    if (node.isLeaf) {
                        // google docs for example excludes a bare '?', 365 does not.
                        // https://twitter.com/egocv/status/1407638341361750021
                        // For now, we behave like office.
                        // TODO: could be interesting to try and make it work the way I think it should. 
                        // TODO: tests for this, many edge cases surely
                        // TODO: space-delimited is two words, right?? gdocs counts as 1
                        wordCount += node.textContent.trim().split(/\s+/).length;
                    }
                });

                return wordCount;
            },
        };
    },
});

export default WordCount;