<template>
  <editor-content
    class="mx-auto border-0 m-10 w-full md:max-w-screen-md"
    :editor="editor"
  />
</template>

<script>
import { Editor, EditorContent } from "@tiptap/vue-2";
import StarterKit from "@tiptap/starter-kit";
import Typography from "@tiptap/extension-typography";
import Placeholder from "@tiptap/extension-placeholder";
import WordCount from "../services/word-count";

export default {
  name: "Editor",
  components: {
    EditorContent
  },

  // TODO: instructions popup
  // TODO: config
  // TODO: save to local storage
  // TODO: disable undo during sprints?
  // TODO: disable copy during sprints? (how? disable selection?)
  // TODO: different fade modes: (whole document goes! / word by word / para by para / sentence by sentence )
  // TODO: focus mode: can only see current para

  data() {
    return {
      editor: null
    };
  },

  props: {
    value: {
      type: String,
      default: ""
    }
  },

  watch: {
    value(value) {
      if (this.editor.getHTML() !== value) {
        this.editor.commands.setContent(this.value, false);
      }
    }
  },

  methods: {
    onEditorUpdate() {
      const content = this.editor.getHTML();
      this.$emit("input", content);
      this.$emit("wc", this.editor.commands.getWordCount());
    }
  },

  mounted() {
    const placeholder =
      "Your session will begin once you start writing!\n" +
      "Each session will be 5 minutes of writing followed by a 2 minute break\n\n" +
      "Don't pause for too long during a sprint, or your words will fade and be GONE FOREVER.\n\n" +
      "F11 to open full screen, ctrl-b for bold, ctrl-i for italics, mardown headers, etc.";
    this.editor = new Editor({
      extensions: [
        StarterKit,
        Typography,
        Placeholder.configure({ placeholder }),
        WordCount
      ],
      content: this.value,
      onUpdate: this.onEditorUpdate
    });
  },

  beforeDestroy() {
    this.editor.destroy();
  }
};
</script>

